.header {
  width: 100%;
  margin-bottom: 30px;
  padding: 0 20px;

  @media (max-width: 768px) {
    margin-bottom: 10px;
  }
}
.menu__list {
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  font-size: calc(12px + 12 * ((100vw - 320px) / (1280 - 320)));
}